#outdoorCard {
    grid-area: outdoor;
    display: grid;
    grid-template-columns: 400px auto;
    grid-template-rows: 100px auto auto;
    margin: 30px;
    padding: 20px;
    background-color: white;
    height: 53vh;
    border-radius: 20px;
    color: black;
    grid-row: 1;
    grid-column: 2;
  }
  
  #headlinepic img{
      grid-column: 1 /span 2;
      grid-row: 1;
      border-radius: 20px;
      margin: 20px;
      height: 400px;
      width: 350px;
  }
  
  
  #toplineOutdoor {
      grid-column: 2;
      grid-row: 1;
      padding: 20px;
      color: black;
      font-size: xx-large;
      padding-bottom: 0px;
  }
  
  #textOutdoor {
      grid-row: 2;
      grid-column: 2;
      padding-top: 39px;
      padding-left: 20px;
      color: rgb(161, 153, 153);
  }
  
  #minipics {
      grid-row: 3 ;
      grid-column: 2;
      margin-left: 20px;
      margin-bottom: -100px;
  }
  
  #minipics img {
  border-radius: 10px;
  margin-right: 30px;
  }
  